import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Spinner, Button} from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect-promise';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import CenteredDiv from '../components/CenteredDiv';
import FilmListElem from '../components/FilmListElem';

const osname = platform();

class Popular extends React.Component {
  constructor(props) {
		super(props);
    this.state = {
      error: false,
      response: [],
    }
    this.getList = this.getList.bind(this);
	}

  componentDidMount() {

    if (this.props.token.scope.search("friends") === -1){
      this.setState({error: true});
      connect.send("VKWebAppGetAuthToken", {"app_id": 6977050, "scope": "friends"}).then(data => {
        this.setState({tokenWithScope: data.data, error: false});
        connect.send("VKWebAppCallAPIMethod", {"method": "friends.getAppUsers", "params": {"v": 5.95, "access_token":data.data.access_token}})
          .then(data => this.getList(data));
          connect.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
      });
    }
    else{
      connect.send("VKWebAppCallAPIMethod", {"method": "friends.getAppUsers", "params": {"v": 5.95, "access_token":this.props.token.access_token}}).then(data => this.getList(data));
    }
  }

  getList(data){
    var friends = data.data.response;
    fetch('https://cinema.voloshinskii.ru/popular/friends', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({friends: friends})
    }).then(res => res.json())
      .then(json => this.setState({ response: Object.values(json.result), loaded: true, error: false }))
  }

  share(){
    connect.send("VKWebAppTapticImpactOccurred", {"style": "light"});
    connect.send("VKWebAppShare");
  }

	render() {
		return (
      <Panel theme="white" id={this.props.id}>
    		<PanelHeader
    			left={<HeaderButton onClick={this.props.go} data-to="home">
    				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
    			</HeaderButton>}
    		>
    			–ü–æ–ø—É–ª—è—Ä–Ω–æ–µ test
    		</PanelHeader>

        {!this.state.error && !this.state.loaded && <Spinner size="large" style={{marginTop: 30}}/>}
        {this.state.error && <CenteredDiv>–î–ª—è —Ä–∞–±–æ—Ç—ã –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—é –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∏–º–µ—Ç—å –¥–æ—Å—Ç—É–ø –∫ —Å–ø–∏—Å–∫—É –í–∞—à–∏—Ö –¥—Ä—É–∑–µ–π</CenteredDiv>}
        {!this.state.error && this.state.loaded && this.state.response.length == 0 &&
          <CenteredDiv>
            <span>–ù–∏ –æ–¥–∏–Ω –∏–∑ –í–∞—à–∏—Ö –¥—Ä—É–∑–µ–π –µ—â—ë –Ω–µ –ø–æ–ª—å–∑—É–µ—Ç—Å—è –Ω–∞—à–∏–º —Å–µ—Ä–≤–∏—Å–æ–º. –ù–æ —ç—Ç–æ –º–æ–∂–Ω–æ –ª–µ–≥–∫–æ –∏—Å–ø—Ä–∞–≤–∏—Ç—å!</span>
            <Button onClick={this.share} size="xl" style={{width:"90%", margin: "auto"}} level="secondary">–î–∞–≤–∞–π—Ç–µ!</Button>
          </CenteredDiv>
        }
        {!this.state.error && this.state.loaded && this.state.response.length > 0 && <div style={{paddingTop: '35px'}}>{this.state.response.map(item =>{
          return <FilmListElem data-fid={item.data.tmdbId} datafid={item.data.tmdbId} onClick={this.props.openFilm} popularCount={item.count} key={item.data._id} title={item.data.title} image={item.data.image}/>
        })}</div>}

    	</Panel>
		);
	}
}

export default Popular;
